}
.vertical{
padding: 20px;
height: 100%;
vertical-align: middle;
}
.centered {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #ECE8E1;
    -webkit-text-stroke-width: 0.5px;
    -webkit-text-stroke-color: black;
}

